    grid-column: 1 / 3;
}

.gallery-loop:not(.columns-3) .single-gallery-image:nth-child(3) {
    -ms-grid-row: 2;
    -ms-grid-row-span: 1;
    grid-row: 2 / 3;
}

.gallery-loop:not(.columns-3) .single-gallery-image:nth-child(6) {
    -ms-grid-row: 4;
    -ms-grid-row-span: 1;
    grid-row: 4 / 5;
}

