/*=======  Back to top  =======*/

.back-to-top {
    position: fixed;
    right: 0;
    bottom: 40px;
    height: 40px;
    width: 40px;
    text-align: center;
    line-height: 40px;
    background-color: #fcd462;
    z-index: 99;
    color: #fff;
    font-size: 18px;
    opacity: 0;
    visibility: hidden;
}

.back-to-top.active {
    visibility: visible;
    opacity: 1;
    right: 40px;
}

.back-to-top:hover {
    background-color: #fff;
}

.alert.d-block{
    display: block!important;
}

